import React from "react";
import {makeStyles} from "@material-ui/core/styles";
import {Card, Button} from "react-bootstrap"

const useStyles = makeStyles((theme) => ({
  root: {
    width: '100%',
    maxWidth: '36ch',
    backgroundColor: theme.palette.background.paper,
  },
  inline: {
    display: 'inline',
  },
}));

const Member = ({ name, imageLink, pageLink, company, color, role, description }) => {

    return (
      <Card style={{ width: '14rem' }}>
        <div style={{alignItems: "center"}}>
          <Card.Img variant="top" src={imageLink} style={{ alignItems: "center", display: "flex", justifyContent: "center"}}/>
        </div>
        <Card.Body>
          <Card.Title>{name}</Card.Title>
          <Card.Subtitle className="mb-2 text-muted">{role}</Card.Subtitle>
          <Card.Text>
            {description}
          </Card.Text>
          <Button style={{background:color, borderColor:'grey'}}>
            <a href={pageLink} target="_blank" rel="noreferrer" style={{ color: 'black', textDecoration: 'none' }}><span>@ {company}</span></a>
          </Button>
        </Card.Body>
      </Card>
    )
}

export default Member